    height: 300px;
}
.img_about:after {
  content: "";
  background-image: url('https://static.wixstatic.com/media/fd8c12_b4404a6d0fcd45a9a680331a36e9ddca~mv2.jpg/v1/fill/w_1114,h_358,al_c,q_85,usm_0.66_1.00_0.01/fd8c12_b4404a6d0fcd45a9a680331a36e9ddca~mv2.webp')
